.gallery {
  flex-flow: wrap;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.photo-card {
  width: 25%;
  vertical-align: top;
  box-sizing: border-box;
  text-align: center;
  margin-bottom: 20px;
  padding: 10px;
  display: inline-block;
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.info {
  margin-top: 10px;
}

.info-item {
  margin-bottom: 5px;
}

.load-more-hidden {
  visibility: hidden;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(to right, gray, silver);
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

.load-more:hover {
  filter: brightness(1.1);
}

.btn-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

